import { createClient } from '@supabase/supabase-js';
import proj4 from 'proj4';

const supabaseUrl = process.env.REACT_APP_SUPABASE_URL || 'https://placeholder.supabase.co';
const supabaseAnonKey = process.env.REACT_APP_SUPABASE_ANON_KEY || 'placeholder-key';

// Verificar se as variveis de ambiente esto configuradas
const isSupabaseConfigured = supabaseUrl !== 'https://placeholder.supabase.co' && supabaseAnonKey !== 'placeholder-key';

export const supabase = isSupabaseConfigured 
  ? createClient(supabaseUrl, supabaseAnonKey)
  : null;

// Definir o sistema de coordenadas portugus (igual ao visual.html)
proj4.defs(
  "ESRI:102164",
  "+proj=tmerc +lat_0=39.66666666666666 +lon_0=-8.131906111111112 +k=1 +x_0=200000 +y_0=300000 +ellps=intl +units=m +no_defs"
);

// Tipos para os dados das tabelas
export interface WellData {
  id: number;
  data: string;
  codigo: string;
  coord_x_m: number;
  coord_y_m: number;
  altitude_m?: string | number;
  sistema_aquifero?: string;
  estado?: string;
  freguesia?: string;
  created_at: string;
}

export interface ConductivityData extends WellData {
  condutividade: number;
  condcamp20c: number;
}

export interface NitrateData extends WellData {
  nitrato: string;
}

export interface PiezometricData extends WellData {
  nivel_piezometrico: string;
  profundidade_nivel_agua: string | number;
  profundidade_nivel_piezometrico: string;
}

export interface PrecipitationData extends WellData {
  precipitacao_dia_mm: number;
  nome: string;
}

// Funo para converter coordenadas usando proj4js (igual ao visual.html)
export const utmToLatLng = (x: number, y: number): [number, number] => {
  try {
    // Converter de ESRI:102164 (sistema portugus) para WGS84
    const [lon, lat] = proj4("ESRI:102164", "WGS84", [x, y]);
    return [lat, lon]; // Retornar como [lat, lng] para o Leaflet
  } catch (error) {
    console.error('Erro na converso de coordenadas:', error);
    // Fallback para converso aproximada se proj4js falhar
    const lat = 39.5 + (y - 500000) / 1000000;
    const lng = -8.0 + (x - 500000) / 1000000;
    return [lat, lng];
  }
};

// Funo especfica para coordenadas de precipitao (UTM zona 29N)
export const precipToLatLng = (x: number, y: number): [number, number] => {
  try {
    // Tentar diferentes sistemas UTM para encontrar o correto
    const utmSystems = [
      "+proj=utm +zone=29 +ellps=WGS84 +datum=WGS84 +units=m +no_defs", // UTM 29N
      "+proj=utm +zone=30 +ellps=WGS84 +datum=WGS84 +units=m +no_defs", // UTM 30N
      "+proj=utm +zone=28 +ellps=WGS84 +datum=WGS84 +units=m +no_defs", // UTM 28N
    ];
    
    // Testar cada sistema e verificar se as coordenadas fazem sentido para Portugal
    for (const utmSystem of utmSystems) {
      try {
        const [lon, lat] = proj4(utmSystem, "WGS84", [x, y]);
        
        // Verificar se as coordenadas esto dentro de Portugal
        if (lat >= 36.5 && lat <= 42.5 && lon >= -10.0 && lon <= -6.0) {
          console.log(` Sistema UTM correto encontrado para precipitao:`, utmSystem);
          console.log(` Coordenadas convertidas:`, { lat, lon, original: [x, y] });
          return [lat, lon];
        }
      } catch (e) {
        continue; // Tentar prximo sistema
      }
    }
    
    // Se nenhum sistema funcionar, usar UTM 29N como padro
    console.log(` Usando UTM 29N como padro para precipitao`);
    const [lon, lat] = proj4(utmSystems[0], "WGS84", [x, y]);
    return [lat, lon];
    
  } catch (error) {
    console.error('Erro na converso de coordenadas de precipitao:', error);
    // Fallback para converso aproximada
    const lat = 39.5 + (y - 500000) / 1000000;
    const lng = -8.0 + (x - 500000) / 1000000;
    return [lat, lng];
  }
};

// Funo para buscar dados por varivel
export const fetchWellData = async (
  variable: string,
  sistemaAquifero?: string
): Promise<WellData[]> => {
  // Se o Supabase no estiver configurado, retornar array vazio
  if (!supabase) {
    console.warn('Supabase no configurado. Configure as variveis de ambiente REACT_APP_SUPABASE_URL e REACT_APP_SUPABASE_ANON_KEY');
    return [];
  }

  let tableName: string;
  
  switch (variable) {
    case 'condutividade':
      tableName = 'condut_tejo_loc';
      break;
    case 'nitrato':
      tableName = 'nitrato_tejo_loc';
      break;
    case 'profundidade':
      tableName = 'piezo_tejo_loc';
      break;
    case 'precipitacao':
      tableName = 'precipitacao_tejo_loc';
      break;
    default:
      throw new Error(`Varivel no suportada: ${variable}`);
  }
  
  console.log(`Iniciando query para tabela: ${tableName}`);
  
  try {
    // Inicializar query corretamente
    let query = supabase.from(tableName).select('*');
    
    // Filtro especial para precipitao - apenas os 7 cdigos especficos
    if (tableName === 'precipitacao_tejo_loc') {
      const codigosPrecipitacao = [
        '19E/01UG',
        '20E/02UG', 
        '20D/01C',
        '18F/01UG',
        '20E/01C',
        '17G/02G',
        '17G/04UG'
      ];
      console.log(` Aplicando filtro para precipitao - apenas 7 cdigos especficos:`, codigosPrecipitacao);
      query = query.in('codigo', codigosPrecipitacao);
      
      // Debug: verificar estrutura dos dados de precipitao
      console.log(` Verificando estrutura da tabela ${tableName}...`);
    }
    // Aplicar filtro de sistema aqufero se especificado (apenas para variveis que tm esta coluna)
    else if (sistemaAquifero && sistemaAquifero !== 'todos' && tableName !== 'precipitacao_tejo_loc') {
      console.log(`Aplicando filtro sistema_aquifero: ${sistemaAquifero} para varivel: ${variable}`);
      query = query.eq('sistema_aquifero', sistemaAquifero);
      console.log('Filtro aplicado com sucesso');
    } else {
      console.log(`No aplicando filtro sistema_aquifero. sistemaAquifero: ${sistemaAquifero}, variable: ${variable}`);
    }
    
    // Buscar todos os dados usando paginao
    let allData: WellData[] = [];
    let page = 0;
    const pageSize = 1000;
    
    while (true) {
      const { data, error } = await query
        .range(page * pageSize, (page + 1) * pageSize - 1);
      
      if (error) {
        console.error('Erro ao buscar dados:', error);
        throw error;
      }
      
      if (!data || data.length === 0) {
        break; // No h mais dados
      }
      
      // Debug especfico para precipitao - verificar estrutura dos dados
      if (tableName === 'precipitacao_tejo_loc' && page === 0) {
        console.log(` Estrutura dos dados de precipitao (primeiro registro):`, {
          colunas: Object.keys(data[0]),
          exemplo: data[0],
          total_registros: data.length
        });
      }
      
      allData = allData.concat(data);
      
      if (data.length < pageSize) {
        break; // ltima pgina
      }
      
      page++;
    }
    
    console.log(`Query concluda. Total de registros: ${allData.length}`);
    
    // Debug final para precipitao
    if (tableName === 'precipitacao_tejo_loc') {
      console.log(` Dados de precipitao carregados:`, {
        total: allData.length,
        codigos_unicos: Array.from(new Set(allData.map(d => d.codigo))),
        coordenadas_exemplo: allData.slice(0, 3).map(d => ({
          codigo: d.codigo,
          coord_x_m: d.coord_x_m,
          coord_y_m: d.coord_y_m,
          tipo_x: typeof d.coord_x_m,
          tipo_y: typeof d.coord_y_m
        }))
      });
    }
    
    return allData;
    
  } catch (error) {
    console.error('Erro na funo fetchWellData:', error);
    return [];
  }
};

// Funo para verificar valores nicos na coluna sistema_aquifero
export const checkSistemaAquiferoValues = async (variable: string): Promise<string[]> => {
  if (!supabase) {
    console.warn('Supabase no configurado');
    return [];
  }

  let query: any;
  
  switch (variable) {
    case 'condutividade':
      query = supabase.from('condut_tejo_loc');
      break;
    case 'nitrato':
      query = supabase.from('nitrato_tejo_loc');
      break;
    case 'profundidade':
      query = supabase.from('piezo_tejo_loc');
      break;
    default:
      return [];
  }
  
  const { data, error } = await query.select('sistema_aquifero');
  
  if (error) {
    console.error('Erro ao verificar valores sistema_aquifero:', error);
    return [];
  }
  
  const uniqueValues = Array.from(new Set(data.map((row: any) => row.sistema_aquifero).filter(Boolean))) as string[];
  console.log(`Valores nicos em sistema_aquifero para ${variable}:`, uniqueValues);
  return uniqueValues;
};

// Funo para buscar dados histricos de um poo especfico
export const fetchWellHistory = async (
  variable: string,
  codigo: string,
  sistemaAquifero?: string
): Promise<WellData[]> => {
  // Se o Supabase no estiver configurado, retornar array vazio
  if (!supabase) {
    console.warn('Supabase no configurado. Configure as variveis de ambiente REACT_APP_SUPABASE_URL e REACT_APP_SUPABASE_ANON_KEY');
    return [];
  }

  let tableName: string;
  
  switch (variable) {
    case 'profundidade':
      tableName = 'piezo_tejo_loc';
      break;
    case 'condutividade':
      tableName = 'condut_tejo_loc';
      break;
    case 'nitrato':
      tableName = 'nitrato_tejo_loc';
      break;
    case 'precipitacao':
      tableName = 'precipitacao_tejo_loc';
      break;
    default:
      throw new Error(`Varivel no suportada: ${variable}`);
  }
  
  console.log(` Buscando histrico para poo ${codigo} na tabela ${tableName}`);
  
  try {
    // Inicializar query corretamente
    let query = supabase.from(tableName).select('*').eq('codigo', codigo);
    
    // Aplicar filtro de sistema aqufero se especificado (apenas para variveis que tm esta coluna)
    if (sistemaAquifero && sistemaAquifero !== 'todos' && variable !== 'precipitacao') {
      const sistemaMap: { [key: string]: string } = {
        'AL': 'T7-ALUVIES DO TEJO',
        'MD': 'T1-BACIA DO TEJO-SADO / MARGEM DIREITA',
        'ME': 'T3-BACIA DO TEJO-SADO / MARGEM ESQUERDA'
      };
      
      const sistemaReal = sistemaMap[sistemaAquifero];
      if (sistemaReal) {
        query = query.eq('sistema_aquifero', sistemaReal);
        console.log(` Aplicando filtro de sistema aqufero: ${sistemaReal}`);
      }
    }
    
    // Ordenar por data
    query = query.order('data', { ascending: true });
    
    // Buscar todos os dados histricos usando paginao
    let allData: WellData[] = [];
    let page = 0;
    const pageSize = 1000;
    
    console.log(` Iniciando paginao com ${pageSize} registros por pgina`);
    
    while (true) {
      console.log(` Buscando pgina ${page + 1}...`);
      
      const { data, error } = await query
        .range(page * pageSize, (page + 1) * pageSize - 1);
      
      if (error) {
        console.error(' Erro ao buscar histrico do poo:', error);
        throw error;
      }
      
      console.log(` Pgina ${page + 1}: ${data?.length || 0} registros`);
      
      if (!data || data.length === 0) {
        console.log(` Fim dos dados na pgina ${page + 1}`);
        break; // No h mais dados
      }
      
      allData = allData.concat(data);
      console.log(` Total acumulado: ${allData.length} registros`);
      
      if (data.length < pageSize) {
        console.log(` ltima pgina (${data.length} < ${pageSize})`);
        break; // ltima pgina
      }
      
      page++;
    }
    
    console.log(` Histrico final para poo ${codigo}: ${allData.length} registros`);
    
    // Mostrar alguns exemplos dos dados
    if (allData.length > 0) {
      console.log(' Primeiros 3 registros:', allData.slice(0, 3));
      console.log(' ltimos 3 registros:', allData.slice(-3));
    }
    
    return allData;
    
  } catch (error) {
    console.error(' Erro ao buscar histrico do poo:', error);
    return [];
  }
}; 